import React from "react"
import store from "./store.js"
import { graphqlendpoint } from "./graphqlendppoint.js";
export default class IssueAbout extends React.Component {
    static async fetch(){
        const data =graphqlendpoint('query{about}')
        console.log("data"+data);
        return data;
    }
    constructor(){
        super()
        const apidata=store.inital?store.inital:null;
        this.state={data:apidata}
    }
    render(){
        console.log("issue about"+store.inital)
        return(
        <div className="text-center" >
            {JSON.stringify(store.inital)?<h1>{JSON.stringify(store.inital.about)}</h1>:<h1>Error</h1>}
        </div>)
    }
}